import React from 'react';
import {showAlert} from '../../Alert';
import SubmitDataHelpers from '../../helpers/SubmitDataHelpers';
import CookieHelpers from '../../helpers/CookieHelpers';
import CheckPoints from './CheckPoints';
import Participants from './Participants';

import CourseInfoStyle from '../../../css/courses/creatorView/CourseInfoStyle';
import '../../../css/courses/creatorView/CourseInfo.css';

class CourseInfo extends React.Component{
    constructor(props){
        super(props);
        this.state = {};
        this.updateState = this.updateState.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentDidMount(){
        let course = {...this.props.course};
        this.setState(course);
        CourseInfoStyle();     
    }

    componentDidUpdate(){
        CourseInfoStyle();
    }
    
    render(){
        let course = {...this.state};
        if(course.type){
            return(
                <div id="courseInfo" className="courseInfo pt-4">
                    <div className='title pt-5 pb-4'>
                        <h2>{course.event}</h2>
                        <h4>&#9827;&nbsp; {course.type} &nbsp;&#9827;</h4>
                    </div>
                    <form onSubmit={this.handleSubmit} className="row col-12 col-sm-10 mx-auto d-flex justify-content-between">
                        <input className="ciInput col-5 my-1 px-2 px-sm-3 border-bottom" type="text" name="event" 
                            value={course.event}  
                            disabled 
                        />
                        <select className="ciInput col-5 my-1 px-2 px-sm-3 border-bottom" name="type" defaultValue={course.type} disabled>
                            <option value="Line">Line</option>
                            <option value="Scatter">Scatter</option>
                            <option value="Score">Score</option>
                        </select>
                        <input className="ciInput col-5 my-1 px-2 px-sm-3 border-bottom" type="text" name="courseAccessKey" 
                            value={course.password}
                            onChange={this.updateState}
                            maxLength="4" 
                            placeholder="Access key (4 characters only)..."
                            required
                        />
                        <input className="ciInput col-5 my-1 px-2 px-sm-3 border-bottom" type="time" name="startTime"
                            value={course.startTime} 
                            onFocus={e => e.target.type='time'}
                            onChange={this.updateState}
                            placeholder="Start time..." 
                            required 
                        />
                        <input className="ciInput col-5 my-1 px-2 px-sm-3 border-bottom" type="time" name="endTime" 
                            value={course.endTime}
                            onFocus={e => e.target.type='time'} 
                            onChange={this.updateState}
                            placeholder="End time..." 
                            required 
                        />
                        <input className="ciInput col-5 my-1 px-2 px-sm-3 border-bottom" type="text" name="status"
                            value="Coming..." 
                            placeholder="Status" 
                            disabled
                        />
                        <input className="ciInput col-5 my-1 px-2 px-sm-3 border-bottom" type="number" name="checkPointNo" 
                            value={course.checkPointNo}
                            onChange={this.updateState}
                            placeholder="Enter number of checkpoints" 
                            min="0" 
                            required 
                        />
                        <div className="ciInput col-5 my-1 d-flex align-items-end justify-content-center">
                            <button className="ciSubmit button input col-10" type="submit">
                                {window.innerWidth < 576? <i className='fas fa-save'></i> : 'Save Change'} 
                            </button>
                        </div>
                    </form>

                    <div className='ciInput row col-12 col-sm-10 my-1 mx-auto d-flex align-items-end justify-content-between'>
                        <Participants 
                            event={{
                                eventName: course.event,
                                courseType: course.type,
                            }}
                        />

                        <CheckPoints
                            event={{
                                eventName: course.event,
                                courseType: course.type
                            }}
                            checkPoints={{
                                checkPointNo: course.checkPointNo,            
                                firstCheckPoint: course.firstCheckPoint,
                            }} 
                        />
                    </div>
                </div>
            );
        }

        return null;
    }

    updateState(e){
        let state = {...this.state};

        switch(e.target.name){
            case 'event':
                state.event = e.target.value;
                this.setState(state);
                break;
            case 'courseAccessKey':
                state.password = e.target.value;
                this.setState(state);
                break;
            case 'startTime':
                state.startTime = e.target.value;
                this.setState(state);
                break;
            case 'endTime':
                state.endTime = e.target.value;
                this.setState(state);
                break;
            case 'checkPointNo':
                state.checkPointNo = e.target.value;
                this.setState(state);
                break;    
            default:
                break;
        }
    }

    handleSubmit(e){
        e.preventDefault();
        let form = e.target;
        
        let error = '';
        //console.log(form['checkPointNo'].value);
        if(form['startTime'].value >= form['endTime'].value){
            error = 'Course cannot end before start.';
        } else if(form['courseAccessKey'].value.length < 4){
            error = "Access key must have a length of 4";
        }

        if(error === ''){
            let cookieHelpers = new CookieHelpers;
            
            let helpers = new SubmitDataHelpers;
            helpers.postData('/courses/editcourse/', 
            {
                password: form['courseAccessKey'].value,
                type: form['type'].value,
                event: form['event'].value,
                startTime: form['startTime'].value,
                endTime: form['endTime'].value,
                status: form['status'].value,
                checkPointNo: form['checkPointNo'].value,
                firstCheckPoint: this.state.firstCheckPoint,
            }).then((res) => {
                //console.log(res); // JSON data parsed by `response.json()` call
                if(res.error){
                    //console.log(res.error);
                    showAlert(res.error, 5000);

                    this.setState({...this.state, ['error']: res.error}); 
                } else {
                    helpers.postData('/participants/addParticipant',
                    {
                        eventName: form['event'].value,
                        courseType: form['type'].value,
                        parName: cookieHelpers.getCookie("username"),
                    });

                    this.setState(res); 
                    window.location.reload();
                }
            });
        } else {
            showAlert(error, 5000);
            this.setState({...this.state, ['error']: error}); 
        }
    }
}

export default CourseInfo;