import React from 'react';
import ReactDOM from 'react-dom';
import QRCode from 'qrcode.react';
import JSZip from 'jszip';
import FileSaver from 'file-saver';
import {showAlert} from '../../Alert';
import SubmitDataHelpers from '../../helpers/SubmitDataHelpers';

import CheckPointsStyle from '../../../css/courses/creatorView/CheckPointsStyle';
import '../../../css/courses/creatorView/CheckPoints.css';

class CheckPoints extends React.Component{
    constructor(props){
        super(props);
        this.state = {};

        this.renderQRCodeList = this.renderQRCodeList.bind(this);
        this.clickedqrCodesModal = this.clickedqrCodesModal.bind(this);
        this.removeModal = this.removeModal.bind(this);
    }

    componentDidMount(){
        this.getCheckPoints();
        CheckPointsStyle();
    }

    componentDidUpdate(){
        //CheckPointsStyle();
    }

    async getCheckPoints(){
        let event = {...this.props.event};
        let checkPoints = {...this.props.checkPoints};

        let helpers = new SubmitDataHelpers;
        let res = await helpers.postData('/checkpoints/getCheckPoints',
        {
            checkPointNo: checkPoints.checkPointNo,
            firstCheckPoint: checkPoints.firstCheckPoint,
        }).then(res => {
            if(!res.error){
                res.forEach(checkPoint => {
                    checkPoint.code += '-' + event.eventName;
                    checkPoint.code +=  '-' + event.courseType;
                    checkPoint.code += '-' + checkPoints.firstCheckPoint;
                    checkPoint.code += '-' + checkPoints.checkPointNo;
                    checkPoint.code += '-' + checkPoint.point;
                });
                
                this.setState({checkPoints: res});
                return res;
            } else {
                showAlert(res.error, 5000);
                console.log(res);
                return false;
            }
        });

        return res;
    }

    render(){
        return(
            <button className='button ciSubmit col-5' onClick={this.renderQRCodeList}>
                Show {this.props.checkPoints.checkPointNo}&nbsp;
                {window.innerWidth < 576? <i className='fas fa-qrcode'></i>: 'QRCodes'}
            </button>
        );
    }

    async renderQRCodeList(e){
        e.preventDefault();

        let checkPoints = await this.getCheckPoints();

        let content = [];

        let tbody = [];
        for(let k in checkPoints){
            let checkPoint = checkPoints[k];

            tbody.push(
                <div className="checkpoint tr row py-2" key={checkPoint.id} value={checkPoint.id}>
                    <div className="td col-2 d-flex justify-content-center align-items-center">
                        <span>{checkPoint.id}</span>
                    </div>
                    <div className="td col-6 d-flex justify-content-center align-items-center">
                        <QRCode
                            key={checkPoint.id}
                            id={checkPoint.id}
                            value={checkPoint.code}
                            size={50}
                            level={"H"}   
                        />
                    </div>
                    <div className="td col-4 d-flex justify-content-center align-items-center">
                        <span>
                            <input 
                                type="number" min="0" max="10" defaultValue={checkPoint.point}
                                onChange = {(e) => {this.updateScore(e, k)}}
                            />
                        </span>
                    </div>
                </div>
            );
        }

        content.push(
            <div key="checkpoints" id="checkpoints" 
                className="checkpoints card shadow-lg pt-4 px-3 px-sm-4" 
                onMouseDown={this.clickedqrCodesModal}
            >
                <div className="card-title mt-3">
                    <h2>{this.props.event.eventName}</h2>
                    <h4>&#9827;&nbsp; {this.props.event.courseType} &nbsp;&#9827;</h4>
                </div>

                <div className="checkpointsTable mx-auto mb-4 mt-2 mt-sm-4 shadow px-2 pt-2 pb-0 border">
                    <div className="tcaption text-left pl-1 pt-1 pb-2">QRCodes ( {checkPoints? checkPoints.length : '0'} qrCode(s) )</div>
                    <div className="table mx-auto border">
                        <div className="thead col-12">
                            <div className="tr row py-3">
                                <div className="th col-2">ID</div>
                                <div className="th col-6">QRcode</div>
                                <div className="th col-4">Point</div>
                            </div>
                        </div>
                        <div id="qrCodes" className="tbody col-12 overflow-auto">
                            {tbody}
                        </div>
                    </div>

                    <div className="downloadqrCodes col-12 d-flex justify-content-end">
                        <button className="btn border-bottom col-8 col-sm-5 px-2" onClick={this.downloadQRCode}>
                            {window.innerWidth > 576? 'Download' : <i className='fas fa-download'></i>}&nbsp;
                            {this.props.checkPoints.checkPointNo} QRCodes 
                        </button>
                    </div>
                </div>
            </div>   
        );
        
        let modal = document.createElement("div");
        modal.id = "popUpModal";
        modal.className = "popUpModal";
        modal.onclick = this.removeModal;

        if(window.localStorage.getItem('darkThemeMode') == 'on'){
            modal.className += " dark-popUpModal";   
        }

        document.body.appendChild(modal);
        await ReactDOM.render(content, modal);
        CheckPointsStyle();
        //this.downloadQRCode();
    }

    clickedqrCodesModal(){
        this.setState({...this.state, ['showModal']: true});
        //console.log(this.state.showModal);
    }

    removeModal(){
        //console.log(this.state.showModal);
        if(!this.state.showModal){
            document.getElementById("popUpModal").remove();
        } else{
            this.setState({...this.state, ['showModal']: null});
        }
    }

    updateScore(e, k){
        let checkPoints = this.state.checkPoints;
        checkPoints[k].point = e.target.value;
        checkPoints[k].code = checkPoints[k].code.substring(0, checkPoints[k].code.length - 2) + '-' + e.target.value;

        this.setState({checkPoints: checkPoints});
        //console.log(this.state.checkPoints);
    }

    updateMsg(e, k){
        //console.log(e.target.value);
    }

    downloadQRCode(){
        let eventName = window.sessionStorage.getItem("eventName");
        let courseType = window.sessionStorage.getItem("courseType");
        let jszip = new JSZip();
        let folder = jszip.folder("QRCodes(" + eventName + "-" + courseType +")");
        let qrCodes = document.getElementById("qrCodes").getElementsByTagName("canvas");
        //console.log(qrCodes);
        Object.keys(qrCodes).forEach(i =>{
            let link = document.createElement('a');
            let base64URL = qrCodes[i].toDataURL().replace(/^data:image\/(png|jpg);base64,/, "")
            folder.file('check_point_'+ i + '.jpg', base64URL, {base64:true})
        });

        jszip.generateAsync({type:"blob"})
        .then(function(content) {
            // see FileSaver.js
            FileSaver.saveAs(content, "QRCodes(" + eventName + "-" + courseType + ").zip");
        });

        //document.getElementById("courseQRCodes").remove();
    }
}

export default CheckPoints;