import React from 'react';
import ReactDOM from 'react-dom';
import {showAlert} from '../../Alert';
import SubmitDataHelpers from '../../helpers/SubmitDataHelpers';

import ParticipantsStyle from '../../../css/courses/creatorView/ParticipantsStyle';
import '../../../css/courses/creatorView/Participants.css';
class Participants extends React.Component{
    constructor(props){
        super(props);
        this.state = {};
        this.showParticipants = this.showParticipants.bind(this);
        this.clickedParticipantModal = this.clickedParticipantModal.bind(this);
        this.removeModal = this.removeModal.bind(this);
        this.addNewParticipant = this.addNewParticipant.bind(this);
        this.deleteParticipant = this.deleteParticipant.bind(this);
    }

    componentDidMount(){
        //console.log(this.props);
        this.getParticipants();
        ParticipantsStyle();
    }

    componentDidUpdate(){
        //ParticipantsStyle();
    }

    getParticipants(){
        let event = {...this.props.event};
        let helpers = new SubmitDataHelpers;
        helpers.postData('/participants/getParticipants',
            {
                eventName: event.eventName,
                courseType: event.courseType,
            }
        ).then(res => {
            //console.log(res);
            if(!res.error){
                this.setState({participants: res});
            } else {
                //console.log(res);
                showAlert(res.error, 5000);

                this.setState({error: res.error});
            }
        });
    }

    render(){
        return(
            <button className='button ciSubmit col-5' onClick={this.showParticipants}>
                Show {this.state.participants? this.state.participants.length: 0}&nbsp;
                {window.innerWidth < 576? <i className='fas fa-users'></i>: 'participants'}
            </button>
        );
    }

    showParticipants(e){
        if(e){
            e.preventDefault();
        }

        /*if(document.getElementById("participants-wrap")){
            document.getElementById("participants-wrap").remove();
        }*/

        if(document.getElementById("popUpModal")){
            document.getElementById("popUpModal").remove();
        }

        let parNo = 1;
        let tbody = [];
        if(this.state.participants){
            //console.log(this.state.participants);
            let participants = this.state.participants;

            participants.forEach(participant => {
                tbody.push(
                    <div className="participant tr row py-2" 
                        key={participant} value={participant}
                        onContextMenu={e => this.deleteParticipant(e, participant)}
                    >
                        <div className="td col-3">{parNo}</div>
                        <div className="td col-9">
                            {participant}
                            {participant == window.sessionStorage.username? 
                                        '' : <i className="deletePar fas fa-minus-circle" 
                                                data-toggle="tooltip" data-placement="top" title="Delete participant..."
                                                onMouseDown={e => this.deleteParticipant(e, participant)}
                                            ></i>
                            }
                        </div>
                    </div>
                );

                parNo++;
            });
        } else {
            showAlert(this.state.error);
        }

        let content = [];
        content.push(
            <div key="participants" id="participants" className="participants card shadow-lg pt-4 px-3 px-sm-4" onMouseDown={this.clickedParticipantModal}>
                <div className="card-title mt-3">
                    <h2>{this.props.event.eventName}</h2>
                    <h4>&#9827;&nbsp; {this.props.event.courseType} &nbsp;&#9827;</h4>
                </div>
                <div className="parTable mx-auto mb-4 mt-2 mt-sm-4 shadow px-2 pt-2 pb-0 border">
                    <div className="tcaption text-left pl-1 pt-1 pb-2">Participants ( {this.state.participants? this.state.participants.length : '0'} player(s) )</div>
                    <div className="table mx-auto border">
                        <div className="thead col-12">
                            <div className="tr row py-3">
                                <div className="th col-3">No.</div>
                                <div className="th col-9">Username</div>
                            </div>
                        </div>
                        <div className="tbody col-12 overflow-auto">
                            {tbody}
                        </div>
                    </div>

                    <form className="addPar col-12 d-flex justify-content-end" autoComplete="off" onSubmit={this.addNewParticipant}>
                        <input className="input border-bottom col-5 col-sm-5 px-2" type="text" name="parName" placeholder="Add player..." autoFocus />
                    </form>
                </div>
            </div>
        );

        let modal = document.createElement("div");
        modal.id = "popUpModal";
        modal.className = "popUpModal";
        modal.onclick = this.removeModal;

        if(window.localStorage.getItem('darkThemeMode') == 'on'){
            modal.className += " dark-popUpModal";   
        }

        document.body.appendChild(modal);
        ReactDOM.render(content, modal);
        ParticipantsStyle();
    }

    clickedParticipantModal(){
        this.setState({...this.state, ['showModal']: true});
        //console.log(this.state.showModal);
    }

    removeModal(){
        //console.log(this.state.showModal);
        if(!this.state.showModal){
            document.getElementById("popUpModal").remove();
        } else{
            this.setState({...this.state, ['showModal']: null});
        }
    }

    addNewParticipant(e){
        e.preventDefault();

        let event = {...this.props.event};
        let helpers = new SubmitDataHelpers;
        helpers.postData('/participants/addParticipant',
            {
                eventName: event.eventName,
                courseType: event.courseType,
                parName: e.target["parName"].value,
            }
        ).then(res => {
            //console.log(res);
            if(!res.error){
                this.setState({participants: res});
                this.setState({error: null});
                this.showParticipants();
            } else {
                showAlert(res.error, 5000);

                this.setState({error: res.error});
                //this.setState({participants: null});
            }
        });
    }

    deleteParticipant(e, participant){
        if(participant == window.sessionStorage.username){
            showAlert('You cannot remove event creator..');
            return false;
        }

        e.preventDefault();
        e.target.innerHTML = "<del>" + e.target.innerHTML + "</del>";
        let event = {...this.props.event};
        let helpers = new SubmitDataHelpers;
        helpers.postData('/participants/deleteParticipant',
            {
                eventName: event.eventName,
                courseType: event.courseType,
                parName: participant,
            }
        ).then(res => {
            //console.log(res);
            if(!res.error){
                this.setState({participants: res});
                this.setState({error: null});
            } else {
                showAlert(res.error, 5000);
                
                this.setState({error: res.error});
                this.setState({participants: null});
            }

            this.showParticipants();
        });
    }
}

export default Participants;