import {eId, eClass, darkLightMode} from '../../../components/helpers/StylingHelpers';

function CheckPointsStyle(){
    setDarkLightMode();
    setStyle();

    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    if (!(/android/i.test(userAgent))) {
        window.addEventListener('resize', setStyle);
    }
}

export default CheckPointsStyle;

function setDarkLightMode(){
    darkLightMode("checkpoints");
    darkLightMode('input');
    darkLightMode('button');
}

function setStyle(){
    if(!eId('qrCodes')){
        return false;
    }

    eId("checkpoints").style.height = window.innerHeight * 0.8 + 'px';

    if(window.innerWidth > window.innerHeight){
        eId('checkpoints').style.borderRadius = parseInt(eId('checkpoints').offsetHeight) * 0.07 + 'px';
    } else {
        eId('checkpoints').style.borderRadius = parseInt(eId('checkpoints').offsetWidth) * 0.07 + 'px';
    }

    let checkpointsTable = eClass('checkpointsTable')[0];
    checkpointsTable.style.height = (eId("checkpoints").offsetTop + eId("checkpoints").offsetHeight - checkpointsTable.offsetTop) * 0.8 + 'px';
    eClass('table')[0].style.height = 0 ;
    eClass('table')[0].style.height = (eClass('downloadqrCodes')[0].offsetTop - eClass('table')[0].offsetTop) * 1 - checkpointsTable.offsetHeight * 0.03 + 'px';
    eClass('tbody')[0].style.height = eClass('table')[0].offsetHeight -  eClass('thead')[0].offsetHeight + 'px';

    setBtn('btn');
}

function setInput(ref){
    let input = eClass(ref);
    for(let i = 0; i < input.length; i++){
        input[i].style.borderRadius = input[0].offsetWidth * 0.05 + 'px';
    }
}

function setBtn(ref){
    let btn = eClass(ref);
    for(let i = 0; i < btn.length; i++){
        btn[i].style.borderRadius = btn[0].offsetWidth * 0.15 + 'px';
    }
}